import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

export default new VueRouter({
	routes: [
		{
			path: "/",
			name: "Home",
			meta: {
				index: 0
			},
			component: () => import('@/components/Home')
		},{
			path: "/trend",
			name: "Trend",
			meta: {
				index: 1
			},
			component: () => import('@/components/Trend')
		},{
			path: "/dapp",
			name: "Dapp",
			meta: {
				index: 2
			},
			component: () => import('@/components/Dapp')
		},{
			path: "/person",
			name: "Person",
			meta: {
				index: 3
			},
			component: () => import('@/components/Person')
		}
	]
});