<template>
  <div id="app">
    <div class="main">
      <transition :name="transitionName">
        <router-view></router-view>
      </transition>
    </div>
    <div class="van-hairline--top" style="width: 100vw;height: 1px;"></div>
    <div>
      <van-tabbar v-model="active" safe-area-inset-bottom>
        <van-tabbar-item to="/" icon="home-o">首页</van-tabbar-item>
        <van-tabbar-item to="/trend" icon="chart-trending-o">增值</van-tabbar-item>
        <van-tabbar-item to="/dapp" icon="apps-o">DAPP</van-tabbar-item>
        <van-tabbar-item to="/person" icon="friends-o">我的</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
  import { Tabbar, TabbarItem } from 'vant'

  export default {
    name: 'app',
    components: {
      [Tabbar.name]: Tabbar,
      [TabbarItem.name]: TabbarItem,
    },
    data(){
      return {
        transitionName: '',
        // shouldKeepAlive: [], //应该被缓存的组件
        // showLoading: false,
        showTabbar: true,
        active: 0,
      }
    },
    watch: {
      $route(to, from) {
        if (to.meta.index > from.meta.index) {
          this.transitionName = "slide-left";
        } else {
          this.transitionName = "slide-right";
        }
      }
    }
  }
</script>

